var event = require('./event');
var request = require('./request');
var tabs = require('./tabs');

/*
    view
 */

var User = require('./user'),
    SignIn = require('./signIn'),
    Gather = require('./gather'),
    globalEvent = require('./global.event'),
    Tabs = require('./tabs'),
    Spend = require('./spend'),
    tabs = null,
    Toast = require('./toast'),
    Hint = require('./hint');
var toast = new Toast({
    el: document.body
});

var user = signIn = gather = spend = null ;

//tabs
$(function(){
    tabs =  new Tabs(".tabs");
    event.on(globalEvent.tabs.switch, function(e, idx){
        tabs.goto(idx);
    });

    /*
        
     */
    var img = ['/public/images/g_s.png', '/public/images/g_m.gif', '/public/images/g_e.gif'],
        image = new Image(),
        i = 0,
        l = img.length;
    image.onload = function(){
        i++;
        i < l && (image.src = img[i]);
    };
    image.src = img[i];

/*
    @res {object}
    @first{bool}  truefalse
 */
var refresh = function(res, first){
    var userModel = {
        nickname: res.nickname,
        logourl: res.logourl,
        coinnum: res.coinnum
        },
        signInModel = {
            signnum: res.signnum,//
            tmrsignnum: res.tmrsignnum,//
            signmark: res.signmark //01
        },
        gatherModel = {
            pitmark: res.pitmark,// 0 1 2
            digseconds: res.digseconds,//	 
            digtime: res.digtime, //N
            digmaxtime: res.digmaxtime, //N
            digbytecoin: res.digbytecoin //bigtimeN
        };
    if(typeof first != 'undefined'){
        user = new User({
            el: '#user',
            model: userModel
        });
        signIn = new SignIn({
            el: '#signin',
            model: signInModel
        });
        gather = new Gather({
            el: '#gather',
            model: gatherModel
        });

        tabs.$el.show();
    }else{
        event.trigger(globalEvent.user.render, userModel);
        event.trigger(globalEvent.signIn.render, signInModel);
        event.trigger(globalEvent.gather.render, gatherModel);
    }
};
//
//

request.get(request.bytecoin.get).done(function(res){
    refresh(res, true);
}).done(function(){
    event.on(globalEvent.bytecoin.render,function(e){
        request.get(request.bytecoin.get).done(function(res){
            //
            refresh(res);
        });
    });
}).fail(function(){
    toast.show(Hint.ERROR_MSG);
});


//
spend = new Spend({
    el: '#spend'
});

});


